<template>
  <div class="chat-page flex-column">
    <div class="chat-head align-center no-shrink no-grow">
      <el-avatar
        class="avatar"
        size="small"
        shape="square"
        icon="el-icon-user-solid"
      ></el-avatar>
      {{ `${id}` }}
    </div>
    <div class="padding-x-16 chat-body limit-with flex-1">
      <div v-if="calling" class="d-flex">
        <PeerVideo ref="myVideo" />
        <PeerVideo ref="remoteVideo" />
      </div>

      <div v-else>
        <ChatMessage
          v-for="(item, idx) in currentMsgList"
          :message="item"
          :key="idx"
        />
      </div>
    </div>
    <el-form
      v-loading="loading"
      :inline="true"
      :model="formInline"
      ref="dynamicValidateForm"
      class="demo-form-inline d-flex padding-x-16 limit-with no-shrink no-grow"
    >
      <el-form-item
        prop="text"
        class="input-list"
        :rules="{
          required: true,
          trigger: 'blur',
        }"
      >
        <el-input
          v-model="formInline.text"
          placeholder="Type your message..."
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('dynamicValidateForm')"
          >SEND</el-button
        >
      </el-form-item>
    </el-form>
    <el-upload
      v-show="false"
      accept="image/*"
      class="avatar-uploader"
      action="#"
      :on-success="handleAvatarSuccess"
      :before-upload="beforeAvatarUpload"
    >
      <i
        ref="upload"
        slot="trigger"
        class="el-icon-plus avatar-uploader-icon"
      ></i>
    </el-upload>
    <p class="tool">
      <i class="el-icon-picture" @click="uploadImg"></i>
      <!-- <i class="el-icon-video-camera-solid" @click="call"></i> -->
    </p>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import { Peer } from "peerjs";
export default {
  name: "ChatView",
  data() {
    return {
      calling: false,
      id: this.$route.params.id,
      formInline: { text: "", type: "text" },
      imageUrl: "",
      curMsg: {},
      myVideo: {},
      remoteVideo: {},
    };
  },
  computed: {
    ...mapState([
      "userId",
      "userAarry",
      "msgdata",
      "currentChatTime",
      "currentTalkId",
    ]),
    ...mapGetters(["currentMsgList"]),
    loading() {
      if (
        this.currentChatTime &&
        !this.msgList.some((v) => v.currentChatTime === this.currentChatTime)
      ) {
        return true;
      }
      return false;
    },
    msgList() {
      return this.msgdata[this.id] || [];
    },
  },
  watch: {
    id() {
      this.$store.commit("setCurrentChatTime", "");
    },
    userAarry(v) {
      if (!v.includes(this.id)) {
        this.$message.error("User is not online");
        this.$router.push("/");
      }
    },
  },
  methods: {
    image2Base64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function () {
          resolve(this.result);
        };
        reader.onerror = function (error) {
          reject(error);
        };
      });
    },
    uploadImg() {
      this.$refs.upload.click();
    },
    handleAvatarSuccess() {},
    async beforeAvatarUpload(file) {
      const isJPG = file.type.includes("image/");
      const isLt5M = file.size / 1024 / 1024 < 5;

      if (!isJPG) {
        this.$message.error(
          "Avatar picture can only be JPG,PNG,JPEG,WEBP format!"
        );
        return false;
      }
      if (!isLt5M) {
        this.$message.error("Avatar picture size cannot exceed 5MB!");
        return false;
      }
      const img = await this.image2Base64(file);
      this.sayTo("image", img);
      return false;
    },
    sayTo(type = "text", img = "") {
      const currentChatTime = `${this.userId}-${new Date().getTime()}`;
      this.curMsg = {
        userId: this.id,
        ...this.formInline,
        src: img,
        type,
        currentChatTime,
        isRead: false,
      };
      this.$store.commit("saveCurrentChat", currentChatTime);
      this.formInline.text = "";
      this.socket.emit("say-to", {
        ...this.curMsg,
      });
    },
    onSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) return;
        this.sayTo();
      });
    },
    call() {
      this.calling = true;
      this.peer = new Peer(this.userId);
      navigator.mediaDevices
        .getUserMedia({ video: true, audio: true }) // 
        .then((stream) => {
          this.$refs.myVideo.loadVideo(stream);
          //  Peer
          const call = this.peer.call(this.id, stream);
          call.on("stream", (remoteStream) => {
            this.$refs.remoteVideo.loadVideo(remoteStream);
          });
        })
        .catch((error) => {
          console.error("Error accessing media devices.", error);
        });
    },
  },
  async created() {
    this.$store.commit("setSomeKey", {
      key: "currentTalkId",
      value: this.id,
    });
    this.$store.commit("saveReadMsg", this.id);
  },
  mounted() {
    document.querySelector(".chat-page").style.height = `${
      window.innerHeight - (62 + 24)
    }px`;
  },
};
</script>
